<template>
<div>
	<div :style='{"padding":"12px","margin":"10px auto","borderColor":"#dbd9f4","borderRadius":"8px","background":"#fff","borderWidth":"0 0 2px","width":"1200px","borderStyle":"solid"}' class="breadcrumb-preview">
		<el-breadcrumb :separator="''" :style='{"width":"100%","margin":"0 auto","fontSize":"14px","lineHeight":"1","display":"flex"}'>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item>{{id ? '' : ''}}</el-breadcrumb-item>
		</el-breadcrumb>
	</div>
	
	<div class="add-preview" :style='{"width":"1200px","margin":"10px auto","position":"relative","background":"none"}'>
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm" :style='{"border":"2px solid #dbd9f4","padding":"40px 20px","borderRadius":"8px","background":"#fff"}'>
			
			<el-form-item label="" prop="recordDate">
				<el-date-picker
					v-model="ruleForm.recordDate"
					type="datetime"
					placeholder=""
					value-format="yyyy-MM-dd HH:mm:ss"
					:style='{"width":"100%"}'>
				</el-date-picker>
			</el-form-item>
			
			<el-form-item label="" prop="mealType">
				<el-radio-group v-model="ruleForm.mealType">
					<el-radio label="breakfast"></el-radio>
					<el-radio label="lunch"></el-radio>
					<el-radio label="dinner"></el-radio>
					<el-radio label="snack"></el-radio>
				</el-radio-group>
			</el-form-item>
			
			<el-form-item label="" prop="foodName">
				<el-input v-model="ruleForm.foodName" placeholder="" clearable></el-input>
			</el-form-item>
			
			<el-form-item label=" (kcal)" prop="calories">
				<el-input-number v-model="ruleForm.calories" :min="0" :max="99999" label=""></el-input-number>
			</el-form-item>
			
			<el-form-item label=" (g)" prop="protein">
				<el-input-number v-model="ruleForm.protein" :min="0" :max="9999" :precision="1" label=""></el-input-number>
			</el-form-item>
			
			<el-form-item label=" (g)" prop="carbs">
				<el-input-number v-model="ruleForm.carbs" :min="0" :max="9999" :precision="1" label=""></el-input-number>
			</el-form-item>
			
			<el-form-item label=" (g)" prop="fat">
				<el-input-number v-model="ruleForm.fat" :min="0" :max="9999" :precision="1" label=""></el-input-number>
			</el-form-item>
			
			<el-form-item label="" prop="notes">
				<el-input type="textarea" v-model="ruleForm.notes" placeholder="" :rows="4"></el-input>
			</el-form-item>
			
			<el-form-item>
				<el-button type="primary" @click="submitForm('ruleForm')" :style='{"cursor":"pointer","border":"0","padding":"0px 30px","margin":"0 10px 0 0","outline":"none","color":"#666","borderRadius":"6px","background":"linear-gradient(90deg, rgba(255,233,100,1) 0%, rgba(194,248,126,1) 29%, rgba(181,233,252,1) 61%, rgba(246,172,218,1) 100%)","width":"auto","fontSize":"14px","lineHeight":"42px","height":"42px"}'></el-button>
				<el-button @click="resetForm('ruleForm')" :style='{"cursor":"pointer","border":"0","padding":"0px 30px","margin":"0 10px 0 0","outline":"none","color":"#666","borderRadius":"6px","background":"#f0f0f0","width":"auto","fontSize":"14px","lineHeight":"42px","height":"42px"}'></el-button>
				<el-button @click="back()" :style='{"cursor":"pointer","border":"0","padding":"0px 30px","margin":"0 10px 0 0","outline":"none","color":"#666","borderRadius":"6px","background":"#f0f0f0","width":"auto","fontSize":"14px","lineHeight":"42px","height":"42px"}'></el-button>
			</el-form-item>
		</el-form>
	</div>
</div>
</template>

<script>
export default {
	data() {
		return {
			id: '',
			ruleForm: {
				recordDate: '',
				mealType: 'breakfast',
				foodName: '',
				calories: 0,
				protein: 0,
				carbs: 0,
				fat: 0,
				notes: ''
			},
			rules: {
				recordDate: [
					{ required: true, message: '', trigger: 'change' }
				],
				mealType: [
					{ required: true, message: '', trigger: 'change' }
				],
				foodName: [
					{ required: true, message: '', trigger: 'blur' }
				],
				calories: [
					{ required: true, message: '', trigger: 'blur' }
				]
			}
		}
	},
	created() {
		this.id = this.$route.query.id;
		if (this.id) {
			this.getInfo();
		} else {
			// 
			this.ruleForm.recordDate = this.formatDateTime(new Date());
		}
	},
	methods: {
		// 
		getInfo() {
			this.$http.get(`dietstatistics/info/${this.id}`).then(res => {
				if (res.data && res.data.code === 0) {
					this.ruleForm = res.data.data;
				}
			});
		},
		// 
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					let userid = this.$storage.get('userid');
					this.ruleForm.userid = userid;
					
					let url = this.id ? 'dietstatistics/update' : 'dietstatistics/save';
					this.$http.post(url, this.ruleForm).then(res => {
						if (res.data && res.data.code === 0) {
							this.$message.success(this.id ? '' : '');
							this.$router.push('/index/dietStatisticsList');
						} else {
							this.$message.error(res.data.msg);
						}
					});
				} else {
					return false;
				}
			});
		},
		// 
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		// 
		back() {
			this.$router.go(-1);
		},
		// 
		formatDateTime(date) {
			let d = new Date(date);
			return d.getFullYear() + '-' + 
				   String(d.getMonth() + 1).padStart(2, '0') + '-' + 
				   String(d.getDate()).padStart(2, '0') + ' ' +
				   String(d.getHours()).padStart(2, '0') + ':' +
				   String(d.getMinutes()).padStart(2, '0') + ':' +
				   String(d.getSeconds()).padStart(2, '0');
		}
	}
}
</script>

<style scoped>
.el-form {
	max-width: 800px;
	margin: 0 auto;
}
</style>
