<template>
  <div>
    <div :style='{"padding":"12px","margin":"10px auto","borderColor":"#dbd9f4","borderRadius":"8px","background":"#fff","borderWidth":"0 0 2px","width":"1200px","borderStyle":"solid"}' class="breadcrumb-preview">
      <el-breadcrumb :separator="''" :style='{"width":"100%","margin":"0 auto","fontSize":"14px","lineHeight":"1","display":"flex"}'>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    
    <div class="receipt-preview" :style='{"width":"1200px","margin":"10px auto","position":"relative","background":"none"}'>
      
      <el-card :style='{"marginBottom":"20px"}'>
        <div slot="header">
          <span :style='{"fontSize":"18px","fontWeight":"bold"}'> </span>
        </div>
        
        <div class="upload-area">
          <el-upload
            class="upload-demo"
            drag
            :action="uploadUrl"
            :headers="headers"
            :on-success="handleUploadSuccess"
            :on-error="handleUploadError"
            :before-upload="beforeUpload"
            :show-file-list="false"
            accept="image/*">
            <i class="el-icon-upload" style="font-size: 67px; color: #C0C4CC;"></i>
            <div class="el-upload__text"><em></em></div>
            <div class="el-upload__tip" slot="tip" style="font-size: 12px; color: #606266; margin-top: 7px;"> JPGPNG </div>
          </el-upload>
          
          <div v-if="uploading" :style='{"textAlign":"center","marginTop":"20px"}'>
            <i class="el-icon-loading" :style='{"fontSize":"32px","color":"#409EFF"}'></i>
            <div :style='{"marginTop":"10px","color":"#666"}'>...</div>
          </div>
        </div>
      </el-card>
      
      <el-card v-if="recognizedItems.length > 0">
        <div slot="header">
          <span :style='{"fontSize":"18px","fontWeight":"bold"}'> </span>
          <el-button style="float: right; padding: 3px 0" type="text" @click="clearResults"></el-button>
        </div>
        
        <div :style='{"marginBottom":"20px"}'>
          <el-alert title="" type="info" :closable="false"></el-alert>
        </div>
        
        <el-form :inline="true" :style='{"marginBottom":"20px","padding":"15px","background":"#f5f7fa","borderRadius":"4px"}'>
          <el-form-item label="">
            <el-date-picker v-model="commonPurchaseDate" type="datetime" placeholder="" value-format="yyyy-MM-dd HH:mm:ss" :style='{"width":"200px"}'></el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="commonStatus" placeholder="" :style='{"width":"120px"}'>
              <el-option label="" value="new"></el-option>
              <el-option label="" value="used"></el-option>
            </el-select>
          </el-form-item>
          <el-button type="primary" size="small" @click="applyCommonSettings"></el-button>
        </el-form>
        
        <el-table :data="recognizedItems" border :style='{"width":"100%"}'>
          <el-table-column type="index" label="" width="60"></el-table-column>
          <el-table-column label="" width="200">
            <template slot-scope="scope">
              <el-input v-model="scope.row.shicaiName" size="small"></el-input>
            </template>
          </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <el-input-number v-model="scope.row.quantity" :min="1" size="small" :style='{"width":"100%"}'></el-input-number>
            </template>
          </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <el-select v-model="scope.row.unit" size="small" :style='{"width":"100%"}'>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-date-picker v-model="scope.row.purchaseDate" type="datetime" size="small" value-format="yyyy-MM-dd HH:mm:ss" :style='{"width":"100%"}'></el-date-picker>
            </template>
          </el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-date-picker v-model="scope.row.expiryDate" type="datetime" size="small" value-format="yyyy-MM-dd HH:mm:ss" :style='{"width":"100%"}'></el-date-picker>
            </template>
          </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <el-select v-model="scope.row.status" size="small" :style='{"width":"100%"}'>
                <el-option label="" value="new"></el-option>
                <el-option label="" value="used"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-button type="danger" size="mini" @click="removeItem(scope.$index)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        
        <div :style='{"marginTop":"20px","textAlign":"center"}'>
          <el-button type="primary" size="large" @click="batchAdd" :loading="saving">
            <i class="el-icon-check"></i>  ({{recognizedItems.length}})
          </el-button>
          <el-button size="large" @click="back"></el-button>
        </div>
      </el-card>
      
      <el-card v-if="recognizedItems.length === 0" :style='{"marginTop":"20px"}'>
        <div slot="header">
          <span :style='{"fontSize":"18px","fontWeight":"bold"}'> </span>
        </div>
        <div :style='{"lineHeight":"2"}'>
          <p><strong>1. </strong></p>
          <p><strong>2. </strong></p>
          <p><strong>3. </strong></p>
          <p><strong>4. </strong></p>
          <p><strong>5. </strong>""</p>
          <p :style='{"color":"#E6A23C","marginTop":"10px"}'>
            <i class="el-icon-warning"></i> OCR
          </p>
        </div>
      </el-card>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      baseUrl: '',
      uploadUrl: '',
      headers: {},
      uploading: false,
      saving: false,
      recognizedItems: [],
      commonPurchaseDate: '',
      commonStatus: 'new'
    }
  },
  created() {
    this.baseUrl = this.$config.baseUrl;
    this.uploadUrl = this.baseUrl + '/ocr/recognizeReceipt';
    this.commonPurchaseDate = this.formatDateTime(new Date());
    
    //  token
    let token = this.$storage.get('Token');
    if (token) {
      this.headers = {
        'Token': token
      };
    }
  },
  methods: {
    beforeUpload(file) {
      const isImage = file.type.indexOf('image') !== -1;
      const isLt5M = file.size / 1024 / 1024 < 5;
      
      if (!isImage) {
        this.$message.error('');
        return false;
      }
      if (!isLt5M) {
        this.$message.error(' 5MB');
        return false;
      }
      
      this.uploading = true;
      return true;
    },
    
    handleUploadSuccess(response, file, fileList) {
      this.uploading = false;
      
      if (response.code === 0) {
        const items = response.items || [];
        
        if (items.length === 0) {
          this.$message.warning('');
          return;
        }
        
        this.recognizedItems = items.map(item => ({
          shicaiName: item.name || '',
          quantity: item.quantity || 1,
          unit: item.unit || '',
          purchaseDate: this.commonPurchaseDate,
          expiryDate: this.calculateExpiryDate(this.commonPurchaseDate, 7),
          status: 'new'
        }));
        
        this.$message.success(` ${items.length} `);
      } else {
        this.$message.error(response.msg || '');
      }
    },
    
    handleUploadError(err, file, fileList) {
      this.uploading = false;
      this.$message.error('' + err.message);
    },
    
    applyCommonSettings() {
      this.recognizedItems.forEach(item => {
        item.purchaseDate = this.commonPurchaseDate;
        item.status = this.commonStatus;
        item.expiryDate = this.calculateExpiryDate(this.commonPurchaseDate, 7);
      });
      this.$message.success('');
    },
    
    calculateExpiryDate(purchaseDate, days) {
      if (!purchaseDate) return '';
      let date = new Date(purchaseDate);
      date.setDate(date.getDate() + days);
      return this.formatDateTime(date);
    },
    
    removeItem(index) {
      this.recognizedItems.splice(index, 1);
    },
    
    clearResults() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.recognizedItems = [];
      });
    },
    
    batchAdd() {
      for (let item of this.recognizedItems) {
        if (!item.shicaiName) {
          this.$message.error('');
          return;
        }
        if (!item.purchaseDate) {
          this.$message.error('');
          return;
        }
      }
      
      this.saving = true;
      let userid = this.$storage.get('userid');
      
      let items = this.recognizedItems.map(item => ({
        ...item,
        userid: userid
      }));
      
      this.$http.post('usershicai/batchSave', items).then(res => {
        this.saving = false;
        if (res.data && res.data.code === 0) {
          this.$message.success('');
          this.$router.push('/index/usershicaiList');
        } else {
          this.$message.error(res.data.msg || '');
        }
      }).catch(() => {
        this.saving = false;
        this.$message.error('');
      });
    },
    
    back() {
      this.$router.go(-1);
    },
    
    formatDateTime(date) {
      let d = new Date(date);
      return d.getFullYear() + '-' + 
             String(d.getMonth() + 1).padStart(2, '0') + '-' + 
             String(d.getDate()).padStart(2, '0') + ' ' +
             String(d.getHours()).padStart(2, '0') + ':' +
             String(d.getMinutes()).padStart(2, '0') + ':' +
             String(d.getSeconds()).padStart(2, '0');
    }
  }
}
</script>

<style scoped>
.upload-area {
  padding: 20px;
}
.upload-demo {
  text-align: center;
}
.upload-demo >>> .el-upload {
  width: 100%;
}
.upload-demo >>> .el-upload-dragger {
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.el-upload__text {
  font-size: 14px;
  color: #606266;
  margin-top: 10px;
}
.el-upload__text em {
  color: #409EFF;
  font-style: normal;
}
</style>
